/* src/App.css */


/* Body */
.App {
  padding-left: 20px;
  padding-right: 20px;
  max-width: 1200px; 
  margin: 0 auto; 
  background-color: #034951;
}

body {
  background-color: #003135;
}

@media (max-width: 768px) {
  .App {
    padding-left: 10px;
    padding-right: 10px;
  }
}

/* About */

.about {
  padding-top: 2rem;
  padding-left: 20px;
  padding-right: 20px;

  color: #AEDDE5;
}

.about-container {
  display: flex;
  align-items: flex-start; /* Vertically aligns items */
  justify-content: space-between; /* Space between image and text */
  gap: 20px; /* Adds some space between the image and text */
  padding: 20px;
}

.about-image img {
  max-width: 100%;
  height: auto;
  border-radius: 10px; /* Optional: Add rounded corners to the image */
  width: 300px; /* Adjust the width based on your preference */
}

.about-text {
  padding-left: 1rem;
  flex: 1; /* Makes the text take up the remaining space */
}

.about-text h2 {
  font-size: 2rem;
  margin-bottom: 10px;
}

.about-text p {
  font-size: 1.1rem;
  line-height: 1.6;
}

/* Projects */
/* Center the header */
.projects-header {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #AEDDE5;
}

/* Styling the project list container */
.project-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  padding: 20px;
}

/* Each project item styling */
.project-item {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.project-item h3 {
  margin-bottom: 10px;
  font-size: 1.5rem;
  color: #444;
}

.project-item p {
  font-size: 1rem;
  color: #666;
  line-height: 1.5;
}

/* Contact */
.contact-details {
  text-align: center; /* Center the contact details */
  margin-top: 20px; /* Space between header and contact details */
}

.contact-item {
  font-size: 1.2rem; /* Adjust font size */
  color: #AEDDE5; /* Color for text */
  margin: 10px 0; /* Vertical spacing between items */
}

.contact-item a {
  color: #007bff; /* Color for links */
  text-decoration: none; /* Remove underline from links */
}

.contact-item a:hover {
  text-decoration: underline; /* Underline on hover */
}


/* Navigation bar */
.nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* padding: 10px 20px; */
  /* background-color: #333; */
  color: #AEDDE5;
}

.nav-logo h1 {
  margin: 0;
  font-size: 3rem;
  font-weight: bold;
}

.nav-pages ul, .nav-links ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.nav-pages ul li, .nav-links ul li {
  margin: 0 10px;
}

.nav-pages ul li a, .nav-links ul li a {
  color: #AEDDE5;
  text-decoration: none;
  font-size: 16px;
  transition: color 0.3s;
}

.nav-pages ul li a:hover, .nav-links ul li a:hover {
  color: #964834;
}

/* Make the navigation bar responsive */
@media (max-width: 768px) {
  .nav-pages ul, .nav-links ul {
    flex-direction: column;
  }
}
